<template>
  <div ref="pixiContainer"></div>
  <div v-if="character">
    
    <p>First name: {{ character.firstName }}</p>
    <p>Surename: {{ character.lastName }}</p>
    <p>Sex: {{ character.gender }}</p>
    <p>DOB: {{ character.birthDate }}</p>
    <p>Nationality: {{ character.nationality }}</p>
    <p v-if="character.nationality !== character.countryOfResidence">
      Country of residence: {{ character.countryOfResidence }}
    </p>
    <p>Address: {{ character.address.street }}, {{ character.address.city }}</p>
    <!-- do poprawy character.imigration ale to na przyszłość -->
    <p v-if="character.imigration">Imigration: {{ character.imigration }}</p>

    <p v-if="character.legit.issue && (character.legit.issue).includes('Wanted')" style="color: red;"><b>WARNING!</b>  {{ character.legit.issue }}</p>

    <!-- debug -->
    <p v-if="character.legit.issue" style="color: red;">Issue: {{ character.legit.issue }}</p>
    <!-- Character complete data: {{ character }} -->
    <!-- end debug -->
    
  </div>
</template>

<script>
import * as PIXI from 'pixi.js';
import logoImg from '../assets/logo.png';
import { generatePersonData } from '@/functions/personDataGeneration';



export default {
  mounted() {
    // Inicjalizacja aplikacji Pixi.js
    this.app = new PIXI.Application({
      width: 150,
      height: 50,
      transparent: true
    });
    this.$refs.pixiContainer.appendChild(this.app.view);

    // Tworzenie grafiki przycisku
    const texture = PIXI.Texture.from(logoImg);
    const button = new PIXI.Sprite(texture);

    // Dodanie interaktywności
    button.eventMode = 'dynamic'; // Ustawienie trybu zdarzeń na 'dynamic'
    button.on('pointerdown', this.onButtonClick);

    // Dodanie przycisku do sceny Pixi.js
    this.app.stage.addChild(button);
  },
  data() {
    return {
      character: null,
    };
  },
  methods: {
    onButtonClick() {
      let generatedPerson = generatePersonData();
      console.log(generatedPerson.person)
      this.character = { ...generatedPerson.person };
    },

  },
  beforeDestroy() {
    // Czyszczenie zasobów Pixi.js
    this.app.destroy(true, { children: true });
  }
};
</script>
