const borderLocationCode = getborderLocationCountryCode(borderLocation);

export function generatePersonData() {
  //losowanie narodowości
  const nationalitySeed = Math.random();

  if (nationalitySeed < localResidentPossibility) {
    person.nationality = borderLocationCode;
  } else {
    person.nationality = locales[Math.floor(Math.random() * locales.length)];
  }

  const nationalityData = allFakers[person.nationality]; //zmienna techniczna

  //losowanie płci
  person.gender = Math.random() > genderPossibility ? "male" : "female";

  //losowanie kraju zamieszkania
  person.countryOfResidence;
  const countryOfResidenceSeed = Math.random();

  //opisy szans poniżej trochę niedokładne ale odpowiadają kodowi
  if (countryOfResidenceSeed < localResidentPossibility) {
    //70% szansy na zamieszkanie w kraju pochodzenia
    person.countryOfResidence = person.nationality;
  } else if (countryOfResidenceSeed < borderLocationResitentPossibility) {
    //20% szansy na zamieszkanie w kraju lokalizacji punktu granicznego
    person.countryOfResidence = borderLocationCode;
  } else {
    //10% szansy na inny adres zamieszkania
    person.countryOfResidence =
      locales[Math.floor(Math.random() * locales.length)];
  }
  const countryOfResidenceData = allFakers[person.countryOfResidence]; //zmienna techniczna

  // ***Generowanie danych osobowych***

  person.firstName = nationalityData.person.firstName(person.gender);
  person.lastName = nationalityData.person.lastName(person.gender);

  //poprawne nazwiska dla polski
  try {
    if (
      person.gender == "female" &&
      person.lastName.endsWith("i") &&
      person.nationality == "pl"
    ) {
      person.lastName = person.lastName.slice(0, -1) + "a";
    }
  } catch (e) {
    console.log(e);
  }

  person.birthDate = faker.date
    .birthdate({ min: 16, max: 75, mode: "age" })
    .toLocaleDateString("pl-PL");
  person.bio = nationalityData.person.bio();

  // Logika generowania adresu
  person.nationality = countryNames[person.nationality] || "???";
  person.countryOfResidence = countryNames[person.countryOfResidence] || "???";

  try {
    person.address.street = `${countryOfResidenceData.location.streetAddress(
      true
    )}`;
    person.address.city = `${countryOfResidenceData.location.city()}`;
  } catch (e) {
    console.log("error: ", e);
    // console.log('countryOfResidence',countryOfResidence)
  }

  //generowanie osoby i jej detali
  person.imigration = imigration(person);
  //generowanie ewentualnych problemów
  person.legit = legitness(person);

  //generowanie dokumentów
  person.passport = passport(person);
  person.visa = visa(person);
  person.schoolLetter = schoolLetter(person);

  // później należy dodać jeszcze zawód, rodzinę, - na wniosku visowym
  // przynależność do organizacji T*, nakazy aresztowania, historię podróży, kontrabanda itp

  return { person };
}

function imigration(person) {
  let imigration = {};
  imigration.date = {};
  if (person.countryOfResidence !== borderLocation) {
    const imigrationSeed = Math.random();

    if (imigrationSeed <= 0.2) {
      //Work visa
      imigration.status = "Work";
      imigration.job = {};
      const durationMonths = Math.floor(Math.random() * 22) + 2; // 2 to 24 months
      imigration.duration = `${durationMonths} months`;
      // imigration.date.start = new Date(presentDate);
      // imigration.date.end = new Date(presentDate.setMonth(presentDate.getMonth() + durationMonths));

      imigration.job.companyName = allFakers[borderLocationCode].company.name();
      imigration.job.companyAddress = `${allFakers[
        borderLocationCode
      ].location.city()}, ${allFakers[
        borderLocationCode
      ].location.streetAddress(true)}`;
      imigration.job.companyPhoneNr =
        allFakers[borderLocationCode].phone.number();
      imigration.job.area = allFakers[borderLocationCode].person.jobArea();
      imigration.job.title =
        jobTitles[Math.floor(Math.random() * jobTitles.length)];
      //duration 2m-2y
      //accommodation
      //company details
    } else if (imigrationSeed <= 0.4) {
      //Tourist visa
      imigration.status = "Tourist";
      const durationWeeks = Math.floor(Math.random() * 24) + 1; // 1 to 24 weeks
      imigration.duration = `${durationWeeks} weeks`;
      // imigration.date.start = new Date(presentDate);
      // imigration.date.end = new Date(presentDate.setDate(presentDate.getDate() + (durationWeeks * 7)));
      //duration 1w-6m
      //accommodation
      //place to visit
    } else {
      //Study visa
      imigration.status = "Study";
      // imigration.education = {};
      const durationMonths = Math.floor(Math.random() * 21) + 3; // 3 to 24 months
      imigration.duration = `${durationMonths} months`;
      // imigration.date.start = new Date(presentDate);
      // imigration.date.end = new Date(presentDate.setMonth(presentDate.getMonth() + durationMonths));
      imigration.education = {};

      // Obliczenie wieku

      const birthDateParts = person.birthDate.split(".");
      const birthDate = new Date(
        birthDateParts[2], // rok
        birthDateParts[1] - 1, // miesiąc (od 0 do 11)
        birthDateParts[0] // dzień
      );

      // Obliczenie wieku
      let age = presentDate.getFullYear() - birthDate.getFullYear();
      const monthDifference = presentDate.getMonth() - birthDate.getMonth();

      if (
        monthDifference < 0 ||
        (monthDifference === 0 && presentDate.getDate() < birthDate.getDate())
      ) {
        age--;
      }

      // Przypisanie odpowiedniej szkoły w zależności od wieku i długości wizy
      const city = allFakers[borderLocationCode].location.city(); // Wygeneruj nazwę miasta
      const number = Math.floor(Math.random() * 99) + 1; // Wygeneruj numer
      if (durationMonths <= 6) {
        imigration.education.schoolName = languageSchools[
          Math.floor(Math.random() * languageSchools.length)
        ].replace("{{city}}", city);
      } else if (age >= 6 && age <= 15) {
        imigration.education.schoolName = primarySchools[
          Math.floor(Math.random() * primarySchools.length)
        ]
          .replace("{{city}}", city)
          .replace("{{number}}", number);
      } else if (age >= 16 && age <= 18) {
        imigration.education.schoolName = highSchools[
          Math.floor(Math.random() * highSchools.length)
        ]
          .replace("{{city}}", city)
          .replace("{{number}}", number);
      } else if (age >= 19) {
        imigration.education.schoolName = universities[
          Math.floor(Math.random() * universities.length)
        ].replace("{{city}}", city);
        //detale dotyczące studiów
        imigration.education.fieldOfStudy =
          fieldsOfStudy[Math.floor(Math.random() * fieldsOfStudy.length)];
        imigration.education.schoolAddress = `${city}, ${allFakers[
          borderLocationCode
        ].location.streetAddress(true)}`;
        imigration.education.schoolPhoneNr =
          allFakers[borderLocationCode].phone.number();
        imigration.education.studentAccommodation = `${city}, ${allFakers[
          borderLocationCode
        ].location.streetAddress(true)}`;
        //szansa na posiadanie pracy dorywczej
        if (Math.random() < 0.8 && age <= 26) {
          imigration.education.sideJob = {};
          imigration.education.sideJob.area =
            sideCompanyNames[
              Math.floor(Math.random() * sideCompanyNames.length)
            ];
          imigration.education.sideJob.companyName = `${
            imigration.education.sideJob.area
          } ${allFakers[borderLocationCode].company.name()}`;
          imigration.education.sideJob.companyAddress = `${city}, ${allFakers[
            borderLocationCode
          ].location.streetAddress(true)}`;
          imigration.education.sideJob.companyPhoneNr =
            allFakers[borderLocationCode].phone.number();
          imigration.education.sideJob.hoursPerWeek =
            Math.floor(Math.random() * 10) + 6; //6-16
        }
      }

      //duration 3m-2y
      //accommodation
      //school details
    }

    //wiza tranzytowa
    // else {
    //   imigration.status = "Transit";
    //   const durationDays = Math.floor(Math.random() * 7) + 1; // 1 to 7 days
    //   imigration.duration = `${durationDays} days`;
    //   imigration.date.start = new Date(presentDate);
    //   imigration.date.end = new Date(presentDate.setDate(presentDate.getDate() + durationDays));

    //duration 1d-1w
    //accommodation
    //destination
    // }
    return imigration;
  } else if (person.nationality !== borderLocation) {
    return (imigration.status = "Permanent Resident");
  } else { return (imigration.status = "Citizen")}
}

function legitness(person) {
  const legitSeed = Math.random(); // Ziarno legitności
  const isLegit = legitSeed < legitPossobility; // 80% szans na legitność

  let issue = "";

  if (!isLegit) {
    const reasonSeed = Math.random(); // Ziarno problemu
    // Wybór powodu nieligityczności na podstawie ziarna problemu
    if (reasonSeed < 0.2) {
      issue = "document issues"; // 20% szans
    } else if (reasonSeed < 0.4) {
      issue = "smuggling - often travels"; // Kolejne 20% szans
    } else if (reasonSeed < 0.6 && person.nationality !== borderLocation) {
      issue = "visa condition violation"; // Kolejne 20% szans
    } else if (reasonSeed < 0.8 && person.nationality !== borderLocation) {
      issue = "missing required information"; // Kolejne 20% szans
    } else {
      issue =
        "Wanted for " +
        wantedReasons[Math.floor(Math.random() * wantedReasons.length)]; // Pozostały % szans
    }
  }

  return { isLegit, issue };
}

